.stars {
  display: flex;

  &__star {
    background-image: url(./images/star.svg);
    background-repeat: no-repeat;
    background-position: 50%;
    height: 16px;
    width: 16px;
  }

  &__star:not(:last-of-type) {
    margin-right: 4px;
  }

  @for $i from 1 through 4 {
    .stars__star:nth-child(-n + #{$i}) {
      background-image: url(./images/star-active.svg);
    }
  }
}
